<template>
  <!--  年龄段分析滚动table -->
  <div >
    <dv-scroll-ranking-board :config="nldfxconfig" style="padding: 10px; height: 165px;" />
  </div>

</template>

<script>
    export default {
      name: "nldfx",
      data(){

        return{
          nldfxconfig:{
            unit: '%',
            data: [
              {
              name: '0-17岁',
              value: 34
            },
              {
                name: '18-25岁',
                value: 36
              },
              {
                name: '25-40岁',
                value: 25
              },
              {
                name: '40-55岁',
                value: 20
              },
              {
                name: '55-70岁',
                value: 12
              },
              {
                name: '70岁以上',
                value: 10
              }]
          },

        }

      }
    }
</script>

<!--suppress CssInvalidFunction -->
<style scoped>

  /deep/ .ranking-value{
    color: #0ecbff;
  }
  /deep/ .dv-scroll-ranking-board .ranking-column .inside-column  {
    position: relative;
    height: 6px;
    border-radius: 5px !important;
    /*background-color: #58fb13;*/
    background: -webkit-linear-gradient(right, #fff400, #00edff);
    background: -o-linear-gradient(right,#fff400,#00edff);
    background: -moz-linear-gradient(right,#fff400,#00edff);
    background: -mos-linear-gradient(right,#fff400,#00edff);
    background: linear-gradient(right,#fff400,#00edff);

    margin-bottom: 2px;
    overflow: hidden;
  }

</style>



