// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

// 依赖库
import ElementUI from 'element-ui';
import VCharts from 'v-charts';
import dataV from '@jiaminghi/data-view'
import echarts from "echarts";
import G2 from '@antv/g2'

import axios from 'axios';
import Api from "./conf/baseURL";     // api接口

// 依赖css
import 'element-ui/lib/theme-chalk/index.css'; // element-ui的css
import 'normalize.css/normalize.css';          // 标准化css
import '@/styles/index.scss';                  // 全局 css
import 'font-awesome/css/font-awesome.css'     // font-awesome 图标

//  use
Vue.use(ElementUI);
Vue.use(VCharts);
Vue.use(dataV);
Vue.use(G2);


//Vue原型对象绑定如下：
Vue.prototype.$echarts = echarts;   // 注册全局Echarts，直接this.$echarts调用
Vue.prototype.$axios = axios;
Vue.prototype.$apiurl = Api;   //
axios.defaults.withCredentials = true; // 如果不加上面这行代码，则session中无法保存任何信息，包括登录信息等等，以至于登录功能无法实现，因此这代码必须加上！


Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});



